.home{
    max-width: 1500px;
    margin-left: auto;
    margin-right: auto;
}

.home__row {
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
}

.home__image{
    width: 100%;
    /* Degrade */
    mask-image: linear-gradient(to top, transparent 1%, black 30%);
    z-index: -1;
    margin-bottom: -250px;
}

